        <p>We do not knowingly solicit data from or market to children under 18 years of age. By using the&nbsp;website, you represent that you are at least 18 or that you are the parent or guardian of such a minor and consent to such minor dependents use of the website. If we learn that personal information from users less than 18 years of age has been collected, we will deactivate the account and take reasonable measures to promptly delete such data from our records. If you become aware of any data we have collected from children under age 18, please contact us at&nbsp;customer-service@simbull.app.</p>
